<template>
  <div class="swiper-container" :class="swipeid">
    <div class="swiper-wrapper">
      <slot></slot>
    </div>
    <!--  -->
    <div :class="{ 'swiper-pagination': pagination }"></div>
  </div>
</template>
<script>
import './swiper'

export default {
  name: 'MSwipe',
  props: {
    swipeid: {
      type: String,
      default: ''
    },
    effect: {
      type: String,
      default: 'slide'
    },
    loop: {
      type: Boolean,
      default: false
    },
    direction: {
      type: String,
      default: 'horizontal'
    },
    pagination: {
      type: Boolean,
      default: true
    },
    autoplay: {
      type: Number,
      default: 5000
    },
    paginationType: {
      type: String,
      default: 'bullets'
    }
  },
  data() {
    return {
      dom: ''
    }
  },
  mounted() {
    const _this = this
    // eslint-disable-next-line no-undef
    this.dom = new Swiper('.' + _this.swipeid, {
      // 
      loop: _this.loop,
      // 
      pagination: '.swiper-pagination',
      // 
      paginationType: _this.paginationType, // fraction,progress,bullets
      // 
      autoplay: _this.autoplay,
      // 
      direction: _this.direction,
      // 
      effect: _this.effect, // slide,fade,coverflow,cube
      autoplayDisableOnInteraction: false,
      observer: true, // swiperswiper
      observeParents: true // swiperswiper
    })
  },
  methods: {
    go(id) {
      this.$router.push({
        path: 'article',
        query: {
          id: id
        }
      })
    }
  }
}
</script>
<style lang="scss">
@import './swiper.min.css';

.swiper-slide {
  height: 7rem;
  overflow: hidden;
  position: relative;
}
.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 1rem;
  width: 95%;
  text-align: right;
}
.swiper-pagination-bullet-active {
  background: #fff;
}

</style>
